:root {
  --bg: #0c1220;
  --card: #121826;
  --indigo-300: #c4b5fd;
}
.app-root {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  z-index: 30;
  pointer-events: auto;
  padding: 1rem;
  width: 100%;
  max-width: 90vw;
}
.logo {
  width: 6rem;
  height: 6rem;
  -o-object-fit: contain;
     object-fit: contain;
}
.player-card {
  width: 100%;
  max-width: 400px;
  height: min(60vh, 300px);
  max-height: calc(100vh - var(--player-height) - 60px);
  background: var(--card);
  border-radius: 1rem;
  padding: 1rem;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.4);
  overflow: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
.empty-message {
  color: #cbd5e1;
  text-align: center;
}
.playlist {
  width: 100%;
  list-style-type: decimal;
  padding-left: 1.5rem;
}
.playlist-item {
  padding: 0.25rem 0;
}
.playlist-item.current {
  color: var(--indigo-300);
}
.file-input-wrapper {
  position: fixed;
  bottom: 28px;
  right: 1.75rem;
  z-index: 40;
}
.file-input {
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  background: var(--bg);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: white;
  font-size: 0.875rem;
  cursor: pointer;
}
.controls-bar {
  position: fixed;
  bottom: 28px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 20;
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  align-items: center;
  background: var(--card);
  padding: 0.75rem;
  border-radius: 1rem;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.35);
}
.control-btn {
  width: 3rem;
  height: 3rem;
  border-radius: 9999px;
  background: #1f2937;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.15s ease;
}
.control-btn:hover {
  background: #2c3446;
}
.icon-img {
  width: 1.25rem;
  filter: brightness(0) invert(1);
}
@media (min-width: 640px) {
  .app-container {
    gap: 1.75rem;
  }
}
@media (min-width: 1024px) {
  .player-card {
    max-width: 520px;
  }
}
:root {
  --bg: #0c1220;
  --card: #121826;
  --player-height: 76px;
  --player-bottom-gap: 28px;
}
html,
body,
#__next {
  height: 100%;
}
body {
  background: var(--bg);
  color: #e6eef8;
  font-family: Inter,
    ui-sans-serif,
    system-ui,
    -apple-system,
    'Segoe UI',
    Roboto,
    'Helvetica Neue',
    Arial;
}
